<template>

    <!---banner----->
    <section class="layout banner-bg">

      <div class="w1000">

        <div class="fth-section fth-banner">


          <div class="fth-header">

            <span class="fth-logo" @click="linkTo()"><img src="../assets/logo.png" width="113" height="88"></span>



            <!--s: language-->
            <div class="fth-lang">

              <div class="curent nav-wap" @click="lang">

                <span class="arrow" :class="langFlag == true ? '' : 'down'"></span>
                <span class="local">{{ language }}</span>
                <span class="flag" :class="nationalFla"></span>

              </div>

              <ul class="fth-lang-ul" :class="langFlag == true ? 'display' : ''">

                <li v-for="item in languages" @click="selectLang(item)">
                  <span class="flag" :class="item.nationalFla"></span>
                  <span class="local">{{ item.language }}</span>
                </li>

              </ul>

            </div>
            <!--e: language-->

            <!--s: 导航-->
            <nav class="fth-nav">

              <ul class="fth-ul">

                <li><a href="javascript:" @click="linkTo()">{{ $t("header.homePage") }}</a></li>
                <li><a href="https://fck.com/#/category?rid=0" target="_blank">{{ $t("header.game") }}</a></li>
                <li><a href="javascript:" class="fth-active">FTH</a></li>
                <li><a href="https://fck.com/#/wallet" target="_blank">{{ $t("header.pay") }}</a></li>
                <li><a href="https://discord.gg/npk7yvF" target="_blank">{{ $t("header.im") }}</a></li>
                <li><a href="https://fck.com/#/about" target="_blank">{{ $t("header.about") }}</a></li>

              </ul>

              <div class="fth-wap">

                <span @click="fthWap"><img src="../assets/icon-9.jpg" width="30" height="30"></span>

                <div class="fth-wap-list" :class="wap == true ? 'display' : ''">
                  <a href="javascript:" @click="linkTo()">{{ $t("header.homePage") }}</a>
                  <a href="https://fck.com/#/category?rid=0">{{ $t("header.game") }}</a>
                  <a href="javascript:" class="fth-active">FTH</a>
                  <a href="https://fck.com/#/wallet">{{ $t("header.pay") }}</a>
                  <a href="https://discord.gg/npk7yvF">{{ $t("header.im") }}</a>
                  <a href="https://fck.com/#/about">{{ $t("header.about") }}</a>
                </div>

              </div>

            </nav>
            <!--e: 导航-->

          </div>

          <h1 class="fth-banner-title">{{ $t("firstFloor.banner") }}</h1>

          <div class="fth-more"><a href="javascript:" @click="more">{{ $t("firstFloor.more") }}</a></div>

        </div>

      </div>

      <!--s: 更多内容弹出框-->
      <div class="mask" :class="mask == false ? 'display' : '' "></div>
      <div class="more-text" :class="mask == false ? 'display' : '' ">
        <a href="javascript:" class="more-a" @click="closePop"><img src="../assets/close.png" width="95" height="95"></a>
        <component :is="wallet"></component>
      </div>
      <!--e: 更多内容弹出框-->


    </section>




</template>

<script>
  import enUS from '../language/details/en-US'
  import zhCN from '../language/details/zh-CN'

  export default {
    name: "banner",
    components:{ enUS,zhCN },
    data(){
      return{
        langFlag:true,
        nationalFla:sessionStorage.getItem("lang") == 'enUS'? 'en-us' : 'zh-cn',
        language:sessionStorage.getItem("lang") == 'enUS' ? 'English' : '简体中文',
        languages:[
          {language:"简体中文",nationalFla:'zh-cn'},
          {language:"English",nationalFla:'en-us'}
        ],
        wap:true,
        mask:false,
        wallet:sessionStorage.getItem("lang"),
      }
    },
    methods:{
      lang(){
        this.langFlag == true ? this.langFlag = false : this.langFlag = true;
        this.wap = true
      },
      selectLang(item){
        console.log(item)
        if(item.nationalFla == 'zh-cn' ){
          this.nationalFla = 'zh-cn';
          this.language = '简体中文';
          this.$i18n.locale = "zhCN";
          this.wallet = 'zhCN'
          sessionStorage.setItem("lang","zhCN")
        }else if(item.nationalFla == 'en-us'){
          this.nationalFla = 'en-us';
          this.language = 'English';
          this.$i18n.locale = "enUS"
          this.wallet = 'enUS';
          sessionStorage.setItem("lang","enUS")
        }
        this.langFlag = true;

      },
      fthWap(){
        this.wap == true ? this.wap = false : this.wap = true;
        this.langFlag = true;
      },
      more(){
        this.mask = true;
      },
      closePop(){
        this.mask = false;
      },
      linkTo(){
        if(sessionStorage.getItem("lang") == 'enUS' || sessionStorage.getItem("lang") == '' || sessionStorage.getItem("lang") == null){
          window.name = 'enUS'
        }else if(sessionStorage.getItem("lang") == 'zhCN'){
          window.name = 'zhCN'
        }
        window.location.href = 'https://fck.com/#/'
      }

    },
    mounted(){
      let _this = this;
      document.addEventListener('click', function (e) {
        // 下面这句代码是获取 点击的区域是否包含你的菜单，如果包含，说明点击的是菜单以外，不包含则为菜单以内
        let flag = e.target.contains(document.getElementsByClassName('fth-lang-ul')[0])
        if(!flag) return;
        _this.langFlag = true;
        _this.wap = true;

      })
    }
  }
</script>

<style scoped>

</style>




