/**
 * validate data
 * @param time minute
 * @returns {boolean}
 */
export function currentTime(time) {

     // let utc = (Math.round(new Date().getTime()/1000)-time)/(60*1000)*1000; //相差分  测试打开

    let utc = (Math.round(new Date().getTime()/1000)-time)/(24*60*60*1000)*1000; //相差天  生产打开

    return utc

}





