          eth:'',
          black:false,
          total:0,
          unlocked:0,
        }
      },
      methods:{
        codeMove(){
          this.code == false ? this.code = true : this.code = false
        },
        onCopy: function () {

          let container = this.$refs.container

          this.$copyText(this.address, container)

          alert( this.$t("secondFloor.code"))

        },
        closeSe(){
          this.black = false
        },
        qrcode () {
          let _this = this;
          let qrcode = new QRCode('qrcode', {
            width: 160,
            height: 160, // 高度
            text: _this.address
          })
        },
        seach(){
          let _this = this;
          if(this.balance == ''){
            alert(this.$t("seachBalance.print"))
          }else{
            this.black = true;
            _this.total = 0;
            _this.unlocked = 0;

            let web3 = new Web3()
            if (window.web3) {
              //如果浏览器中安装有metamask，则执行这里
              web3 = new Web3(window.web3.currentProvider);

            } else {
              web3 = new Web3(new Web3.providers.HttpProvider('https://mainnet.infura.io/v3/9f4f36e6ec284bd8844040a3af1f442a'));

            }

            var abi = [{"constant":true,"inputs":[],"name":"hasClosed","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"unlockPeriod","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"name":"buyTokenAmount","type":"uint256"}],"name":"rewardTokenAmount","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"rate","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"cap","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"name":"beneficiary","type":"address"}],"name":"contributionsOf","outputs":[{"name":"contributeTimes","type":"uint256[]"},{"name":"buyTokenAmounts","type":"uint256[]"},{"name":"rewardTokenAmounts","type":"uint256[]"},{"name":"lastWithdrawTimes","type":"uint256[]"},{"name":"withdrawPercents","type":"uint256[]"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"weiRaised","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"isOpen","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"beneficiary","type":"address"}],"name":"withdrawTokens","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"closingTime","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"capReached","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"wallet","outputs":[{"name":"","type":"address"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"isOver","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"openingTime","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"name":"beneficiary","type":"address"}],"name":"unlockBalanceOf","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"rewardPeriod","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"beneficiary","type":"address"}],"name":"buyTokens","outputs":[],"payable":true,"stateMutability":"payable","type":"function"},{"constant":true,"inputs":[],"name":"token","outputs":[{"name":"","type":"address"}],"payable":false,"stateMutability":"view","type":"function"},{"inputs":[{"name":"rewardPeriod","type":"uint256"},{"name":"unlockPeriod","type":"uint256"},{"name":"cap","type":"uint256"},{"name":"openingTime","type":"uint256"},{"name":"closingTime","type":"uint256"},{"name":"rate","type":"uint256"},{"name":"wallet","type":"address"},{"name":"token","type":"address"}],"payable":false,"stateMutability":"nonpayable","type":"constructor"},{"payable":true,"stateMutability":"payable","type":"fallback"},{"anonymous":false,"inputs":[{"indexed":true,"name":"purchaser","type":"address"},{"indexed":true,"name":"beneficiary","type":"address"},{"indexed":false,"name":"value","type":"uint256"},{"indexed":false,"name":"amount","type":"uint256"}],"name":"TokensPurchased","type":"event"}]


            let contract = web3.eth.contract(abi).at(_this.address);


            contract.contributionsOf(this.balance, function (err, val) {


              if (err) {
                console.log(err);
              } else {
                let totalVal = 0;
                _this.blanceObj = JSON.parse(JSON.stringify(val));
                for(let i = 0;i<_this.blanceObj[0].length;i++){
                  _this.total+=parseInt(_this.blanceObj[1][i])+parseInt(_this.blanceObj[2][i]);
                  totalVal = parseInt(_this.blanceObj[1][i])+parseInt(_this.blanceObj[2][i]);
                  // _this.unlocked += parseInt(_this.blanceObj[1][i])
                  if(currentTime(parseInt(_this.blanceObj[0][i]))<30){
                    _this.unlocked+=web3.fromWei(totalVal,'ether')*0
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>30 && currentTime(parseInt(_this.blanceObj[0][i]))<60){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*0.5
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>60 && currentTime(parseInt(_this.blanceObj[0][i]))<90){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*0.6
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>90 && currentTime(parseInt(_this.blanceObj[0][i]))<120){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*0.7
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>120 && currentTime(parseInt(_this.blanceObj[0][i]))<150){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*0.8
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>150 && currentTime(parseInt(_this.blanceObj[0][i]))<180){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*0.9
                  }else if(currentTime(parseInt(_this.blanceObj[0][i]))>180){
                    _this.unlocked += web3.fromWei(totalVal,'ether')*1
                  }
                }
                _this.total = web3.fromWei(_this.total,'ether');  // 总量=买了多少token+奖励了多少token  买的和奖励的列表总和  数组形式[['买的token','买的token']['奖励的token','奖励的token']]
              }
            });

          }

        },
      },
